<template>
  <v-container fluid pa-r>
    <v-layout row wrap>
      <v-flex xs12>
        <h3 class="headline white--text secondary--text"></h3>
        <h4 class="caption white--text">Build your application today!</h4>
        <p>            .</p>
      </v-flex>

      <v-flex xs12>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field v-model="name" :counter="10" :rules="nameRules" label="Name" required color="#FF0000" append-icon="close" prepend-icon="phone"></v-text-field>
          <v-text-field v-model="email" :rules="emailRules" label="E-mail" required append-icon="close" prepend-icon="mail"></v-text-field>

          <v-select
            v-model="select"
            :items="items"
            :rules="[v => !!v || 'Item is required']"
            label=" "
            required
          ></v-select>

          <v-textarea
          no-resize 
          rows = "9"
          name="comment"
          label="comment"
          value="  "
          hint=" ?"
        ></v-textarea>

          <!--<v-btn :disabled="!valid" color="primary" @click="validate">Validate</v-btn>-->
          <v-btn color="accent" depressed @click="reset"> </v-btn>
        </v-form>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
export default {
  data: () => ({
    valid: true,
    name: "",
    nameRules: [
      v => !!v || "Name is required",
      v => (v && v.length <= 10) || "Name must be less than 10 characters"
    ],
    email: "",
    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+@.+/.test(v) || "E-mail must be valid"
    ],
    select: null,
    items: [" ", " ", " ", "ATTO report"],
    checkbox: false
  }),

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    }
    /* resetValidation() {
      this.$refs.form.resetValidation();
    } */
  }
};
</script>
